

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#canvas {
    width: 1200px;
    height: 700px;
    border: 1px solid black;
    background-image: url(https://images.assetsdelivery.com/compings_v2/midosemsem/midosemsem1903/midosemsem190300006.jpg);
    background-size: cover;
}

#menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: none;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    padding: 50px;
    background: none;
    border: whitesmoke solid 2px;
    border-radius: 2px;
    width: 80%;
    height: 80%;
}

#menu > h1 {
    font-size: 105px;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    margin-top: 5%;
    text-align: center;
    color: white;
    height: 50%;

}

#menu > h2 {
    font-size: 35px;;
    font-family: Arial, Helvetica, sans-serif;
    width: 100%;
    text-align: center;
    margin-top: 1%;
    margin-bottom: 2%;
    color: white;
    height: 10%;

}

#start-button {
    width: 40%;
    border: whitesmoke solid 8px;
    background: purple;
    margin: 10px;
    font-size: 35px;;
    background-color: turquoise;
}

#start-button > h1 {
    border: black;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

#start-button:hover{
    cursor: pointer;
}

.div_class {
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 50px
